import { vehicleService as supabaseVehicleService, statisticsService } from './supabaseService';
import { supabase } from '../supabase/config';

// Cache simple en memoria
let vehicleCache = null;
let cacheTime = null;
const CACHE_DURATION = 30000; // 30 segundos

// Funcin para limpiar cache
export const clearVehicleCache = () => {
  vehicleCache = null;
  cacheTime = null;
  console.log(' Cache de vehculos limpiado');
};

// Obtener todos los vehculos
export const getVehicles = async (filters = {}) => {
  try {
    console.log(' getVehicles: Iniciando consulta...');
    
    // Consulta directa simplificada
    const { data, error } = await supabase
      .from('vehicles')
      .select('*')
      .order('created_at', { ascending: false });
    
    if (error) {
      console.error(' Error de Supabase:', error);
      // Si es error de tabla no existe, devolver array vaco
      if (error.message.includes('relation "vehicles" does not exist')) {
        console.log(' Tabla "vehicles" no existe, devolviendo array vaco');
        return [];
      }
      throw error;
    }
    
    console.log(' Datos obtenidos de Supabase:', data?.length || 0);
    console.log(' Datos:', data);
    
    return data || [];
  } catch (error) {
    console.error(' Error obteniendo vehculos:', error);
    // En lugar de lanzar error, devolver array vaco para que la app funcione
    return [];
  }
};

// Obtener un vehculo por ID
export const getVehicleById = async (id) => {
  try {
    return await supabaseVehicleService.getVehicleById(id);
  } catch (error) {
    console.error('Error obteniendo vehculo:', error);
    throw error;
  }
};

